<template> 
    <div class = "post">
        <button id = "button" @click = "onDelete(post.id)" > X </button>
        <h3> {{post.name}} </h3>
        <p> {{post.text}} </p>
        <h3> {{post.hash_tag}} </h3>
    </div>
</template>

<script>
    export default {
        name: 'post',
        props: {
            post: Object,
        },
        methods: {
            onDelete(id) {
                this.$emit('delete-post', id)
            },
        },
    }
</script>


